var React=require("react");
var Stacktoc=require("ksana2015-stacktoc").component;  //載入目錄顯示元件
var Searcharea=require("./searcharea.jsx");
var tibetan=require("tibetan/wylie");
var tabarea = React.createClass({displayName: "tabarea",
  getInitialState: function() {
    return {res:{},res_toc:[]};
  },
  setBannerHeight:function(bannerHeight) {
    var ch=document.documentElement.clientHeight;
    this.refs["tab-content"].getDOMNode().style.height=(ch-bannerHeight)+"px";//-40
  },
  componentDidUpdate:function()  {
    this.setBannerHeight(100);
  },  
  textConverter:function(t) {
    if(this.props.wylie == true) return tibetan.toWylie(t,null,false); 
    return t; 
  },
  render: function() {
    return (
      React.createElement("div", null, 
        React.createElement("div", {className: "borderright"}, 
          React.createElement("ul", {className: "nav nav-tabs", role: "tablist"}, 
            React.createElement("li", {className: "active"}, React.createElement("a", {href: "#Catalog", role: "tab", "data-toggle": "tab", title: "Catalog"}, React.createElement("img", {width: "25", src: "./banner/icon-read.png"}))), 
            React.createElement("li", null, React.createElement("a", {href: "#Search", role: "tab", onClick: this.startsearch, "data-toggle": "tab", title: "Search"}, React.createElement("img", {width: "25", src: "./banner/search.png"})))
          ), 

          React.createElement("div", {className: "tab-content", ref: "tab-content"}, 
            React.createElement("div", {className: "tab-pane fade in active", id: "Catalog"}, 
              React.createElement(Stacktoc, {className: "stacktoc", textConverter: this.textConverter, showText: this.props.showText, showExcerpt: this.showExcerpt, 
              opts: {tocbar:"banner/bar.png",tocbar_start:"banner/bar_start.png",stopAt:"་",
              maxitemlength:42,tocstyle:"vertical_line"}, 
              hits: this.state.res.rawresult, data: this.props.toc, goVoff: this.state.goVoff})
            ), 

            React.createElement("div", {className: "tab-pane fade", id: "Search"}, 
              React.createElement(Searcharea, {db: this.props.db, wylie: this.props.wylie, gotofile: this.props.gotofile, toc: this.props.toc})
            )
          )
        )
      )
    );
  }
});
module.exports=tabarea;