/** @jsx React.DOM */

/* to rename the component, change name of ./component.js and  "dependencies" section of ../../component.js */

//var othercomponent=Require("other"); 
var React=require("react");
var tibetan=require("tibetan/wylie");
var resultlist=React.createClass({displayName: "resultlist",  //should search result
  show:function() {
    if(this.props.wylie == false) var tofind=this.props.tofind;
    if(this.props.wylie == true ) var tofind=tibetan.toWylie(this.props.tofind,null,false);
    
    try {
      var t = new RegExp(tofind,"g"); 
      return this.props.res.excerpt.map(function(r,i){ // excerpt is an array 
      
        var context="";
        if(this.props.wylie == false) context=r.text;//r.text.replace(t,function(tofind){return "<hl>"+tofind+"</hl>"});
        if(this.props.wylie == true) context=tibetan.toWylie(r.text,null,false).replace(t,function(tofind){return "<hl>"+tofind+"</hl>"});
        return React.createElement("div", {"data-vpos": r.hits[0][0]}, 
        React.createElement("a", {onClick: this.gotopage, className: "pagename"}, r.segname), 
          React.createElement("div", {className: "resultitem", dangerouslySetInnerHTML: {__html:context}})
        )
      },this);
    } catch(e) {
    return null;
  }
  }, 
  gotopage:function(e) {
    var vpos=parseInt(e.target.parentNode.dataset['vpos']);
    this.props.gotofile(vpos,this.props.tofind);
  },
  renderCount:function() {
    var hit=this.props.res.tochit ||this.props.res.rawresult.length;

    return React.createElement("span", null, hit, " hits");
  },
  warnTooMuch:function() {
    if (this.props.res.excerptOverflow)  {
      return React.createElement("div", {className: "alert alert-danger", role: "danger"}, 
        "Please refine your search or click number beside table of content to narrow down the range."
      )
    }
  },
  render:function() {
    if (this.props.res) {
      if (this.props.res.excerpt&&this.props.res.excerpt.length) {
          return React.createElement("div", {className: "results"}, 
                  this.renderCount(), React.createElement("br", null), this.show(), 
                  React.createElement("br", null), this.warnTooMuch()
                  )
      } else {
        return React.createElement("div", null)
      }
    }
    else {
      return React.createElement("div", null, "type keyword to search")
    } 
  }
});
module.exports=resultlist; 